import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import {
  Box,
  Button,
  Card,
  TextField,
  FormGroup,
  FormControl,
  InputLabel,
  Input,
  FormHelperText,
  Typography,
  Stack,
  InputAdornment,
  IconButton,
} from "@mui/material";
import { PROCESSED_ERRORS, onApiError } from "~/utils/errorsProcessor";

import UserService from "~/services/UserService";
import TokenService from "~/services/TokenService";
import AuthService from "~/services/AuthService";
import { routes } from "~/routes";
import styles from "./Profile.module.css";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { onError, setLoading } from "~/state/edit/editState";
import { useDispatch } from "react-redux";

function Profile() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const userId = TokenService.getUser().id;
  const [user, setUser] = useState({});
  const { t } = useTranslation("manage");

  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");

  const [firstName, setFirstName] = useState("");
  const [firstNameError, setFirstNameError] = useState("");

  const [lastName, setLastName] = useState("");
  const [lastNameError, setLastNameError] = useState("");

  const [currentPassword, setCurrentPassword] = useState("");
  const [currentPasswordError, setCurrentPasswordError] = useState("");
  const [newPass, setNewPassword] = useState("");
  const [passNewError, setNewPassError] = useState("");
  const [confrimNewPassword, setConfrimNewPassword] = useState("");
  const [confrimNewPasswordError, setConfrimNewPasswordError] = useState("");

  const [operation, setOprtation] = useState(OPERATION.NONE);

  const [showCurrentPass, setShowCurrentPass] = useState(false);
  const [showNewPass, setShowNewPass] = useState(false);
  const [showConfirmPass, setShowConfirmPass] = useState(false);

  const handleClickShowCurrentPass = () => setShowCurrentPass((show) => !show);
  const handleClickShowNewPass = () => setShowNewPass((show) => !show);
  const handleClickShowConfirmPass = () => setShowConfirmPass((show) => !show);

  const onEmailChanged = (e) => {
    setEmail(e.target.value);
    setEmailError("");
  };

  const onFirstNameChanged = (e) => {
    setFirstName(e.target.value);
    setFirstNameError("");
  };

  const onLastChanged = (e) => {
    setLastName(e.target.value);
    setLastNameError("");
  };

  const onCurrentPasswordChanged = (e) => {
    setCurrentPassword(e.target.value);
    setCurrentPasswordError("");
  };

  const onNewPasswordChanged = (e) => {
    setNewPassword(e.target.value);
    setNewPassError("");
  };

  const onConfirmPasswordChanges = (e) => {
    setConfrimNewPassword(e.target.value);
    setConfrimNewPasswordError("");
  };

  const criticalChange =
    operation === OPERATION.CHANGE_EMAIL ||
    operation === OPERATION.CHANGE_PASSWORD;

  const validate = () => {
    setEmailError("");
    setFirstNameError("");
    setLastNameError("");
    if (firstName.length == 0) {
      setFirstNameError(t("firstname_required"));
      return false;
    }
    if (lastName.length == 0) {
      setLastNameError(t("lastname_required"));
      return false;
    }
    if (email.length == 0) {
      setEmailError(t("email_required"));
      return false;
    }
    if (criticalChange & !currentPassword) {
      setCurrentPasswordError(t("password_required"));
      return false;
    }

    if (newPass != confrimNewPassword) {
      setConfrimNewPasswordError(t("error.password_should_match"));
      return false;
    }
    return true;
  };

  const goUsers = () => {
    navigate(routes.manageUsers);
  };

  const logout = () => {
    AuthService.logout()
      .then(() => {
        navigate(routes.login);
      })
      .catch((e) => {
        navigate(routes.login);
      });
  };

  const onSubmit = () => {
    if (!validate()) {
      return;
    }
    dispatch(setLoading(true));

    const data = {};

    if (user.firstName !== firstName) {
      data.firstName = firstName;
    }

    if (user.lastName !== lastName) {
      data.lastName = lastName;
    }

    if (user.email !== email) {
      data.email = email;
    }

    if (currentPassword !== "") {
      data.password = currentPassword;
    }

    if (newPass !== "") {
      data.newPassword = newPass;
    }

    UserService.updateUserProfile(data)
      .then(() => {
        if (criticalChange) {
          logout();
        }
      })
      .catch((e) => {
        onApiError({
          error: e,
          globalErrorHandler: (processedError) => {
            dispatch(onError(processedError));
          },
          locallErrorHandler: (processedError) => {
            if (processedError.name == PROCESSED_ERRORS.DUPLICATE_EMAIL.name) {
              setEmailError(t(`processed_errors.${processed.name}`));
            } else if (
              processedError.name == WRONG_CREDENTIALS.INVALID_SURVEY_DATES.name
            ) {
              setCurrentPasswordError(t("processed_errors.wrong_password"));
            }
          },
        });
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  useEffect(() => {
    UserService.getUser({ userId: userId }).then((response) => {
      setUser(response);
      setEmail(response.email);
      setFirstName(response.firstName);
      setLastName(response.lastName);
    });
  }, []);
  return (
    <Box className={styles.mainContainer}>
      <Card className={styles.createUserCard}>
        <Typography variant="h4" sx={{ mb: "10px" }}>
          {t("profile.title")}
        </Typography>
        <Stack flexDirection="row" gap={5} alignItems="center">
          <FormGroup className={styles.formGroup}>
            <TextField
              error={firstNameError.length > 0}
              required={true}
              value={firstName}
              id="firstName"
              label={t("label.first_name")}
              name="firstName"
              onChange={onFirstNameChanged}
              variant="standard"
            />
            <TextField
              error={lastNameError?.length > 0}
              id="lastName"
              value={lastName}
              required={true}
              onChange={onLastChanged}
              label={t("label.last_name")}
              name="lastName"
              variant="standard"
            />

            <TextField
              disabled={operation != OPERATION.CHANGE_EMAIL}
              required={true}
              value={email}
              error={emailError?.length > 0}
              id="email"
              onChange={onEmailChanged}
              label={t("label.email")}
              name="email"
              variant="standard"
            />

            {operation == OPERATION.CHANGE_PASSWORD && (
              <>
                {" "}
                <FormControl
                  required={true}
                  variant="standard"
                  sx={{ mt: "10px" }}
                >
                  <InputLabel htmlFor="standard-adornment-password">
                    {t("label.new_password")}
                  </InputLabel>
                  <Input
                    error={passNewError.length > 0}
                    onChange={onNewPasswordChanged}
                    name="password"
                    value={newPass}
                    type={showNewPass ? "text" : "password"}
                    id="standard-adornment-password"
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowNewPass}
                        >
                          {showNewPass ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                </FormControl>
                <FormControl
                  required={true}
                  variant="standard"
                  sx={{ mt: "10px" }}
                >
                  <InputLabel htmlFor="standard-adornment-password">
                    {t("label.confirm_new_password")}
                  </InputLabel>
                  <Input
                    error={confrimNewPasswordError.length > 0}
                    onChange={onConfirmPasswordChanges}
                    name="cpassword"
                    type={showConfirmPass ? "text" : "password"}
                    value={confrimNewPassword}
                    id="standard-adornment-password"
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowConfirmPass}
                        >
                          {showConfirmPass ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                </FormControl>
              </>
            )}

            {criticalChange && (
              <>
                <FormControl
                  required={true}
                  variant="standard"
                  sx={{ mt: "10px" }}
                >
                  <InputLabel htmlFor="standard-adornment-password">
                    {t("label.current_password")}
                  </InputLabel>
                  <Input
                    error={currentPasswordError.length > 0}
                    onChange={onCurrentPasswordChanged}
                    name="password"
                    type={showCurrentPass ? "text" : "password"}
                    value={currentPassword}
                    id="standard-adornment-password"
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowCurrentPass}
                        >
                          {showCurrentPass ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                </FormControl>
                <FormHelperText>
                  {t("profile.password_required")}
                </FormHelperText>
              </>
            )}
            {(firstNameError ||
              lastNameError ||
              emailError ||
              currentPasswordError ||
              passNewError ||
              confrimNewPasswordError) && (
              <FormHelperText className={styles.errorText}>
                {firstNameError ||
                  lastNameError ||
                  emailError ||
                  currentPasswordError ||
                  passNewError ||
                  confrimNewPasswordError}
              </FormHelperText>
            )}
          </FormGroup>
          {criticalChange && (
            <Box
              className={styles.hint}
              sx={{ maxWidth: "300px", fontSize: "20px" }}
            >
              {t("profile.hint")}
            </Box>
          )}
        </Stack>
        <Box sx={{ display: "flex", justifyContent: "flex-end", mt: "40px" }}>
          {operation == OPERATION.NONE && (
            <Button onClick={() => setOprtation(OPERATION.CHANGE_EMAIL)}>
              {t("profile.change_email")}
            </Button>
          )}
          {operation == OPERATION.NONE && (
            <Button onClick={() => setOprtation(OPERATION.CHANGE_PASSWORD)}>
              {t("profile.change_password")}
            </Button>
          )}
          {criticalChange && (
            <Button onClick={() => setOprtation(OPERATION.NONE)}>
              {t("profile.cancel")}
            </Button>
          )}
          <Button onClick={onSubmit}>{t("profile.save")}</Button>
        </Box>
      </Card>
    </Box>
  );
}

const OPERATION = {
  NONE: {},
  CHANGE_EMAIL: {},
  CHANGE_PASSWORD: {},
};

export default Profile;
